import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material';
import { CHARTCONFIG } from '../charts/charts.config';

import ApiCompanyFindModel from '../_api/company/find/output';
import ApiCompanyFindService from '../_api/company/find/service';
import ApiProviderFindModel from '../_api/provider/find/output';
import ApiProviderFindService from '../_api/provider/find/service';
import ApiSwRatesModel from '../_api/sw/rates/output';
import ApiSwRatesService from '../_api/sw/rates/service';
import ApiSwWalletModel from '../_api/sw/wallet/output';
import ApiSwWalletService from '../_api/sw/wallet/service';

import DashboardTransferDialogComponent from './components/transfer-dialog';

import SessionService  from '../_services/session';

import { SailsService } from 'angular2-sails';
const ClientConfig = require('../../../config/client');

@Component({
  selector: 'my-dashboard',
  template: require('./dashboard.component.html')
})
export class DashboardComponent implements OnInit {
  company: ApiCompanyFindModel = null;
  dataProviders: ApiProviderFindModel[];
  rates: ApiSwRatesModel = null;
  wallet: ApiSwWalletModel = null;

  constructor(
    private apiCompanyFind: ApiCompanyFindService,
    private apiProviderFind: ApiProviderFindService,
    private apiSwWallet: ApiSwWalletService,
    private apiSwRates: ApiSwRatesService,
    private dialog: MatDialog,
    private sailsService: SailsService,
    private session: SessionService,
  ) {}

  balance: number
  config = CHARTCONFIG;
  currency = [{
    code: 'EUR',
    name: 'Euro'
  }, {
    code: 'MXN',
    name: 'Mexican Peso'
  }, {
    code: 'USD',
    name: 'US Dollar'
  }]
  transfer = {
    sourceAmount: 10.00,
    destCurrency: 'MXN',
    destAmount: 10.00,
    provider: '',
  }

  ngOnInit() {
    this.balance = 399000.00;
    this.sailsService.connect(ClientConfig.api);

    this.apiCompanyFind.exec({
      id: this.session.user.company,
    }).subscribe(company => {
      this.company = company[0];

      if (this.company.wallet.id !== undefined) {
        this.apiSwWallet.exec({
          id: this.company.wallet.id,
        }).subscribe(wallet => {
          this.wallet = wallet;
        }, err => {});

        this.sailsService.get('/wallet/feed', {
          wallet: this.company.wallet.srn,
        }).subscribe(result => {
          this.sailsService.on('message').subscribe(result => {
            this.wallet.availableBalances['USD'] += result.amount;
          }, err => {});
        }, err => {});
      }
    }, err => {});

    this.apiProviderFind.exec({}).subscribe(result => {
      this.dataProviders = result;
    }, err => {});

    this.apiSwRates.exec({}).subscribe(result => {
      this.rates = result;
    }, err => {});
  }

  submit() {
    const dialogRef = this.dialog.open(DashboardTransferDialogComponent, {
      data: this.transfer,
    });
    dialogRef.afterClosed().subscribe(result => {});
  }
}



