import { Injectable } from '@angular/core';
import * as jwt from 'jsonwebtoken';

import UserModel from '../_models/user'

const KEY_NAME = 'user';

@Injectable()
export default class SessionService {
  user: UserModel = null;
  token: string = null;

  constructor() {
    this.token = localStorage.getItem(KEY_NAME);
    if (this.token !== null) {
      this.user = jwt.decode(this.token);
    }
  }

  clear(): void {
    this.user = null;
    this.token = null;
    localStorage.removeItem(KEY_NAME);
  }

  set(token): void {
    this.user = jwt.decode(token);
    this.token = token
    localStorage.setItem(KEY_NAME, token);
  }
}


