import { Component, Input } from '@angular/core';

import ApiCompanyFindService from '../../../_api/company/find/service';

import SessionService from '../../../_services/session';

@Component({
  selector: 'my-app-sidenav-menu',
  styles: [],
  template: require('./sidenav-menu.component.html')
})
export class AppSidenavMenuComponent {
  master: boolean = false;

  constructor(
    private apiCompanyFind: ApiCompanyFindService,
    private session: SessionService,
  ) {
    this.apiCompanyFind.exec({
      id: this.session.user.company,
    }).subscribe(company => {
      this.master = company[0].wallet.id === undefined;
    }, err => {});
  }
}



